*,
*::after,
*::before{
  padding:0;
  margin: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  font-family: sans-serif;
}

ul {
  list-style: none;
}


.navigation{
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  position: absolute;
  top:0;
  left: 0;
  width: 100%;
  z-index: 2;

  > li {
    flex: 1;
    background: #eee;
    text-align: center;
    cursor: pointer;

    &:hover {
      background: #ddd;
    }

    a{
      padding: 2rem;
      display: inline-block;
      width: 100%;
      color: #333;
      text-decoration: none;
      font-size: 1.75rem;
      transition: all .25s ease-out;

      &.is-active{
        background:#333;
        color: #fff;
      }
    }
  }
}

.eyeglass {
  pointer-events: none;
  display: block;
  width: calc(100% - 10rem);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 100rem;
  padding-top: 53%;
  z-index: 2;
  
  &__lens {
    width: 36%;
    height: 43%;
    background: #00000054;
    position: absolute;
    top: 50%;
    left: 50%;
    border: 3px solid #2d0b0b;
  
    &--left {
      transform: translate(-105%, -50%);
      border-top-left-radius: 13rem;
      border-bottom-left-radius: 15rem;
      border-top-right-radius: 10rem;
      border-bottom-right-radius: 25rem;
    }
  
    &--right {
      transform: translate(5%, -50%);
      border-top-right-radius: 13rem;
      border-bottom-right-radius: 15rem;
      border-top-left-radius: 10rem;
      border-bottom-left-radius: 25rem;
    }
  }
}


.custom-hotspot {
  background: red;
  height: 50px;
  width: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  cursor: pointer;
  border:5px solid #ddd;
}

.custom-tooltip{
  color: blue;

  span{
    opacity: 0;
    width: 100px;
    height: 100px;
    position: absolute;
    top: -110px;
    left: 50%;
    background: #fff;
    transform: translateX(-50%);
    border-radius: .5rem;
    padding: 1rem;
    box-sizing: border-box;
    color: #000;

    transition: all .25s ease-in;
  }

  &:hover {
    span{
      opacity: 1;
    }
  }
}