.interactive{
    position: absolute;
    top: 5.5rem;
    height: calc(100vh - 5.5rem);
    width: 100%;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    


    &__control {
        margin: 1rem;
        border-radius: .5rem;
        overflow: hidden;
        display: inline-block;
    }

    &__control-button {
        border: none;
        padding: 1rem;
        background: #ddd;
        cursor: pointer;
        outline: none;
        transition: all .25s ease-out;

        &--active{
            background: #333;
            color: #fff;
        }
    }


    &__video-container{
        width: calc(100% - 5rem);
        height: 40rem;
        background: #eee;
        position: relative;
        overflow: hidden;
        max-width: 70rem;

        .curtain{
            overflow: hidden;    
            transition: width 3s ease-out;
            position: absolute;
            top: 0rem;
            box-shadow: 0px 0px 7px 1px #fff;
            z-index: 1;

            &--standard{
                width: 100%;
            }

            &--boost{
                width: 0.1%;
            }
        }
    }
}

.video-type{
    color: #fff;
    position: absolute;
    z-index: 2;
    padding: 1rem;
    font-size: 1.5rem;
    text-transform: uppercase;
    background: #333333cf;
    margin: .5rem;
    border-radius: .25rem;
}

.video-player {
    video {
        width: 70rem;
        height: 40rem;
    }
}