.side-by-side{
    position: absolute;
    top: 6rem;
    height: calc(100vh - 6rem);
    width: 100%;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    &__videos{
        display: flex;
        align-items: center;
        justify-content: center;

        width: calc(100% - 5rem);
        max-width: 108rem;

        video{
            width: 100% !important;
            height: 100%;
        }
    }

    &__video-wrapper {
        box-shadow: -2px 0px 0px 0px #fff;
        z-index: 1;

        label{
            font-size: 1.5rem;
            padding: .5rem 0rem;
            display: inline-block;
        }
    }

    &__slider {
        width: calc(100% - 5rem);
        max-width: 108rem;
        
        input {
            width: 100%;
            outline: none;
        }
    }
}