.filter {
    height: calc(100vh - 6rem);
    margin-top: 6rem;
    background: #000;

    display: flex;
    align-items: center;
    flex-flow: column;
    justify-content: center;

    select{
        height: 4rem;
        padding: 0 2rem;
        margin-bottom: 2rem;
    }

    &__container {
        width: 50rem;
        height: 50rem;
        position: relative;

        .filter-on, .filter-off{
            position: absolute;
            width: 100%;
            left: 0;
            top: 0;
            transition: opacity .5s ease-out;
        }

        .filter-on{
            z-index: 1;            
        }

        .filter-off{
            z-index: 2;
        }

        video, .video-player{
            height: 100%;
        }
    }

    &__trigger{
        position: absolute;
        z-index: 3;
        left: 0;
        top: 0;

        height: 100%;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        
        button {
            border: 1px solid #fff;
            width: 12rem;
            height: 12rem;
            border-radius: 50%;
            outline: none;
            background-color: rgba(#333, .5);
            cursor: pointer;
            color: #fff;
            padding: 1rem;
            transition: all .25s ease-in-out;

            &:hover {
                box-shadow: 0 0 0px 1rem rgba(#fff, .5);
            }

            &:active {
                box-shadow: 0 0 0px 0px transparent;
            }
        }
    }
}